import { combineReducers } from 'redux';

// import * as appConfigReducers from './app-config';
import * as domEventsReducers from './dom-events';
import * as propertiesReducers from './properties';
import * as mockDataReducers from './mock-data';
import * as validationsReducers from './validations';
import * as notificationsReducers from './notifications';

export const reducers = combineReducers({
    // ...appConfigReducers,
    ...propertiesReducers,
    ...mockDataReducers,
    ...domEventsReducers,
    ...validationsReducers,
    ...notificationsReducers
});


