import { SHOW_NOTIFICATION, HIDE_NOTIFICATION, CLEAR_NOTIFICATIONS } from '../actions';

const INITIAL_STATE: INotification[] = [];

export const notifications = (previousState = INITIAL_STATE, action): INotification[] => {
    switch (action.type) {
        case SHOW_NOTIFICATION:
            return previousState.concat(action.payload);
        case HIDE_NOTIFICATION:
            return previousState.filter((notif) => notif.I__D !== action.payload);
        case CLEAR_NOTIFICATIONS:
            return [];
        default:
            return previousState;
    }
};



