import {
    isString, isArray, isObject
} from '@v8187/rs-utils';

const createTRow = (data: any): string => {
    let tr = '<tr>';
    for (let key in data) {
        if (data.hasOwnProperty(key)) {
            tr += `<td>${data[key]}</td>`;
        }
    }
    return tr + '</tr>';
};

export const toHTML = (data: any[]): string => {
    const props = Object.keys(data[0]);

    let ths = '', trs = '';

    props.map(prop => ths += `<th>${prop}</th>`);

    data.map(item => {
        trs += createTRow(item);
    });

    return `<table><thead><tr>${ths}</tr></thead><tbody>${trs}</tbody></table>`;
};

export const toCSV = (data: any[]): string => {
    let csv: string[] = [Object.keys(data[0]).join(',')];
    data.map(objItem => {
        let values: Array<string | number | boolean> = [];
        for (let key in objItem) {
            if (objItem.hasOwnProperty(key)) {
                let val = objItem[key];
                // Wrap the string in double qoutes, if contains comma
                values.push(isString(val) && val.indexOf(',') !== -1 ? `'${val}'` : val);
            }
        }
        csv.push(values.join(','));
    });
    return csv.join('\n');
};

export const toXML = (data, parentKey: string, intend: string): string => {
    let xml = '';
    if (!intend) {
        xml += '<?xml version="1.0" encoding="UTF-8"?>';
    }

    const fnReucrsive = (objItem, tempIntend: string, isList: boolean = false) => {
        if (isList) {
            xml += `\n${tempIntend}<${parentKey || 'data'}Item>`;
        }

        for (const key in objItem) {
            if (objItem.hasOwnProperty(key)) {
                const val = objItem[key];
                if (isArray(val) || isObject(val)) {
                    xml += `\n${tempIntend + '\t'}<${key}>`;
                    xml += toXML(val, key, tempIntend + '\t');
                    xml += `\n${tempIntend + '\t'}</${key}>`;
                } else {
                    xml += `\n${tempIntend + '\t'}<${key}>${val}</${key}>`;
                }
            }
        }
        if (isList) {
            xml += `\n${tempIntend}</${parentKey || 'data'}Item>`;
        }
    };

    if (isArray(data)) {
        if (!intend) {
            xml += `\n<dataList>`;
        }
        data.map(objItem => { fnReucrsive(objItem, intend + '\t', true) });
        if (!intend) {
            xml += `\n</dataList>`;
        }
    } else {
        fnReucrsive(data, intend);
    }

    return xml;
};


