import {
    LOAD_RS_MOCK_MODULE, loadRsMockModule,
    RS_MOCK_MODULE_LOADED, rsMockModuleLoaded
} from '../actions';

export const appConfigMiddleware = ({ dispatch, getState }) => next => action => {
    switch (action.type) {
        case LOAD_RS_MOCK_MODULE:
            import('@v8187/rs-mock').then(rsMockModule => {
                dispatch(rsMockModuleLoaded(rsMockModule));
            });
            break;
        default:
            next(action);
            break;
    }
};


