import { createStore, applyMiddleware } from 'redux';
import { createEpicMiddleware } from 'redux-observable';
import { enableDocClick, enableDocFocus, enableWinResize /*, loadRsMockModule */ } from '../actions';
import { reducers } from '../reducers';
import { rootEpic } from '../epics';
import { /* appConfigMiddleware, */ propertiesMiddleware, /* validationsMiddleware, */ mockDataMiddleware } from '../middlewares';

const epicMiddleware = createEpicMiddleware();

export const appStore = createStore(
    reducers,
    applyMiddleware(/* appConfigMiddleware, */ propertiesMiddleware, /* validationsMiddleware, */ mockDataMiddleware, epicMiddleware)
);

epicMiddleware.run(rootEpic);

const { dispatch } = appStore;
// dispatch(loadRsMockModule());
dispatch(enableDocClick());
dispatch(enableDocFocus());
dispatch(enableWinResize());


