import {
    address, alphanumeric, city, color, company, country, creditCard, currency,
    dateTime, duration, email, gender, latLong, phoneNo,
    password, personName, state, username, randomWords
} from '@v8187/rs-mock';
import {
    xToNum, randomItem, setUID, randomNum
} from '@v8187/rs-utils';

export const getMockValue = (prop, idx: number = 0) => {
    switch (prop.type) {
        case 'aadhaar': return xToNum('xxxxxxxxxx');
        case 'alphanumeric': return alphanumeric({ format: prop.options ? prop.options.format : '' });
        case 'autoIncrement': return idx;
        case 'boolean': return randomItem([true, false]);
        case 'ccPin': return xToNum('xxxx');
        case 'city': return city(prop.options);
        case 'color': return color(prop.options);
        case 'company': return company(prop.options);
        case 'country': return country();
        case 'creditCard': return creditCard(prop.options);
        case 'currency': return currency(prop.options);
        case 'cvv': return xToNum('xxx');
        case 'dateTime': return dateTime(prop.options);
        case 'duration': return duration(prop.options);
        case 'email': return email(prop.options);
        case 'gender': return gender(prop.options);
        case 'latLong': return latLong(prop.options);
        case 'pan': return alphanumeric({ format: 'AAAAAxxxxA' });
        case 'password': return password(prop.options);
        case 'personName': return personName(prop.options);
        case 'phoneNo': return phoneNo(prop.options);
        case 'pincode': return xToNum('xxxxxx');
        case 'randomNumber': return randomNum(prop.options ? prop.options.min : 1, prop.options ? prop.options.max : 100);
        case 'randomWords': return randomWords(prop.options ? prop.options.min : 3, prop.options ? prop.options.max : 20);
        case 'state': return state(prop.options);
        case 'streetAddress': return address();
        case 'uid': return setUID();
        case 'username': return username(prop.options);
        default: return '';
    }
};

export enum DOWNLOAD_FORMAT { CSV, XML, JSON, HTML, MODEL };

export const downloadData = (rawData, format: DOWNLOAD_FORMAT, modelName: string = '') => {

    let blob: Blob,
        fileName: string = `${modelName ? modelName.replace(/\s+/g, '-').toLowerCase().trim() : 'data-model'}-${new Date().valueOf()}-`;

    switch (format) {
        case DOWNLOAD_FORMAT.CSV:
            blob = new Blob([rawData], { type: 'text/csv' });
            fileName = `${fileName}csv.csv`;
            break;
        case DOWNLOAD_FORMAT.HTML:
            blob = new Blob([rawData], { type: 'text/html' });
            fileName = `${fileName}html.html`;
            break;
        case DOWNLOAD_FORMAT.JSON:
        case DOWNLOAD_FORMAT.MODEL:
        default:
            blob = new Blob([JSON.stringify(rawData)], { type: 'octet/stream' });
            fileName = `${fileName}json.json`;
            break;
        case DOWNLOAD_FORMAT.XML:
            blob = new Blob([rawData], { type: 'text/xml' });
            fileName = `${fileName}xml.xml`;
            break;
    };
    if (navigator.msSaveOrOpenBlob) {
        navigator.msSaveOrOpenBlob(blob, fileName);
    } else {
        const url = window.URL.createObjectURL(blob),
            a = document.createElement('a');

        document.body.appendChild(a);
        // a.style = 'display: none';
        a.href = url;
        a.download = fileName;
        a.click();
        a.remove();
        window.URL.revokeObjectURL(url);
    }
};


