// import { combineReducers } from 'redux';

import { DOCUMENT_CLICK, DOCUMENT_FOCUS, WINDOW_RESIZE } from '../actions';

/* export */ const documentClick = (previousState = {}, action) => {
    switch (action.type) {
        case DOCUMENT_CLICK:
            return { event: action.payload };
        // case ENABLE_DOCUMENT_CLICK:
        // case DISABLE_DOCUMENT_CLICK:
        default:
            return previousState;
    }
};

/* export */ const windowResize = (previousState = {}, action) => {
    switch (action.type) {
        case WINDOW_RESIZE:
            return { event: action.payload };
        // case ENABLE_WINDOW_RESIZE:
        // case DISABLE_WINDOW_RESIZE:
        default:
            return previousState;
    }
};

export const domEvents = (previousState = {}, action) => {
    switch (action.type) {
        case DOCUMENT_CLICK:
            return {
                event: action.payload,
                type: 'click'
            };
        case DOCUMENT_FOCUS:
            return {
                event: action.payload,
                type: 'focus'
            };

        case WINDOW_RESIZE:
            return {
                event: action.payload,
                type: 'resize'
            };
        // case ENABLE_DOCUMENT_CLICK:
        // case DISABLE_DOCUMENT_CLICK:
        // case ENABLE_DOCUMENT_FOCUS:
        // case DISABLE_DOCUMENT_FOCUS:
        // case ENABLE_WINDOW_RESIZE:
        // case DISABLE_WINDOW_RESIZE:
        default:
            return previousState;
    }
};
