import { combineEpics } from 'redux-observable';
import {
    documentClickEpic, documentFocusEpic,
    windowResizeEpic
} from './dom-events';

export const rootEpic = combineEpics(
    documentClickEpic, documentFocusEpic,
    windowResizeEpic
);


