import {
    CHANGE_RECORDS_COUNT, CREATE_DATA, CREATE_DATA_SUCCESS,
    DOWNLOAD_CSV_SUCCESS, DOWNLOAD_HTML_SUCCESS, DOWNLOAD_JSON_FAILED,
    DOWNLOAD_JSON_SUCCESS, DOWNLOAD_MODEL_SUCCESS, DOWNLOAD_XML_SUCCESS,
    SHOW_GENERATE_N_DOWNLOAD, HIDE_GENERATE_N_DOWNLOAD, UPDATE_MOCK_SAMPLE
} from '../actions';

const INITIAL_STATE = {
    mockSample: null,
    recordsCount: 10,
    dataJson: null,
    dataSerialized: null,
    dataCreated: false,
    csvDownloaded: false,
    xmlDownloaded: false,
    htmlDownloaded: false,
    jsonDownloaded: false,
    modelDownloaded: false
};

export const showingGenerateNDownloadDialog = (previousState = false, action) => {
    switch (action.type) {
        case SHOW_GENERATE_N_DOWNLOAD:
            return true;
        case HIDE_GENERATE_N_DOWNLOAD:
            return false;
        default:
            return previousState;
    }
};

export const mockData = (previousState = INITIAL_STATE, action) => {
    switch (action.type) {
        case CHANGE_RECORDS_COUNT:
            return {
                ...previousState,
                recordsCount: parseInt(action.payload, 10)
            };
        case CREATE_DATA:
            return {
                ...previousState,
                ...action.payload
            };
        case UPDATE_MOCK_SAMPLE:
            return {
                ...previousState,
                ...action.payload
            };
        case CREATE_DATA_SUCCESS:
            return {
                ...previousState,
                dataCreated: true
            };
        case DOWNLOAD_CSV_SUCCESS:
            return {
                ...previousState,
                csvDownloaded: true
            };
        case DOWNLOAD_HTML_SUCCESS:
            return {
                ...previousState,
                htmlDownloaded: true
            };
        case DOWNLOAD_JSON_SUCCESS:
            return {
                ...previousState,
                jsonDownloaded: true
            };
        case DOWNLOAD_MODEL_SUCCESS:
            return {
                ...previousState,
                modelDownloaded: true
            };
        case DOWNLOAD_XML_SUCCESS:
            return {
                ...previousState,
                xmlDownloaded: true
            };
        case HIDE_GENERATE_N_DOWNLOAD:
            return {
                ...previousState,
                dataJson: null,
                dataSerialized: null,
                dataCreated: false,
                csvDownloaded: false,
                xmlDownloaded: false,
                htmlDownloaded: false,
                jsonDownloaded: false,
                modelDownloaded: false
            };
        default:
            return previousState;
    }
};


