import {
    ADD_PROPERTIES_UI, ADD_PROPERTIES_JSON,
    REMOVE_PROPERTY, CHANGE_PROP_NAME, CHANGE_PROP_TYPE,
    CHANGE_PROP_OPTIONS, SHOW_PROP_OPTIONS, HIDE_PROP_OPTIONS, CHANGE_PROP_PARENT,
    SHOW_JSON_MODEL_FORM, HIDE_JSON_MODEL_FORM, SET_MODEL_NAME, UPDATE_PROP_RELATIONSHIP
} from '../actions';
import { IProperty } from '../../models/property.model';

export const modelName = (previousState: string = '', action) => {

    switch (action.type) {
        case SET_MODEL_NAME:
            return action.payload;
        default:
            return previousState;
    }
};

export const properties = (previousState: any[] = [], action) => {
    const { payload } = action;

    switch (action.type) {
        case ADD_PROPERTIES_JSON:
            return payload;
        case ADD_PROPERTIES_UI:
            return previousState.concat(...payload);
        case CHANGE_PROP_NAME:
            return previousState.map((property: IProperty) => {
                if (property.I__D === payload.I__D) {
                    return payload;
                }
                return property;
            });
        case CHANGE_PROP_TYPE:
            return previousState.map((property: IProperty) => {
                if (property.I__D === payload.I__D) {
                    return payload;
                }
                return property;
            });
        case CHANGE_PROP_PARENT:
            return previousState.map((property: IProperty) => {
                if (property.I__D === payload.I__D) {
                    return payload;
                }
                return property;
            });
        case CHANGE_PROP_OPTIONS:
            return previousState.map((property: IProperty) => {
                if (property.I__D === payload.I__D) {
                    return payload;
                }
                return property;
            });
        case REMOVE_PROPERTY:
            // return previousState.length > 1 ?
            return previousState.filter((prop: IProperty) => prop.I__D !== payload);
        // : previousState;
        case UPDATE_PROP_RELATIONSHIP:
            return action.payload.properties;
        default:
            return previousState;
    }
};

export const showingJsonModelForm = (previousState = false, action) => {
    switch (action.type) {
        case SHOW_JSON_MODEL_FORM:
            return true;
        case HIDE_JSON_MODEL_FORM:
            return false;
        default:
            return previousState;
    }
};

export const selectedProperty = (previousState = null, action) => {
    switch (action.type) {
        case SHOW_PROP_OPTIONS:
            return { ...action.payload };
        case HIDE_PROP_OPTIONS:
            return null;
        default:
            return previousState;
    }
};

export const parentProperties = (previousState: any[] = [], action) => {
    switch (action.type) {
        case UPDATE_PROP_RELATIONSHIP:
            return action.payload.parentProps;
        default:
            return previousState;
    }
};

