import { setUID } from '@v8187/rs-utils';

export interface IPropertyErrors {
    name: {
        required: boolean;
        duplicate: boolean;
    }
};

export interface IProperty {
    I__D: string;
    name: string;
    fullName: string;
    type: string;
    options?: any;
    mockValue?: string | number;
    isParent?: boolean;
    parentRef?: string;
    children?: IProperty[];
    isValid?: boolean;
    errors: IPropertyErrors;
};

export class MProperty implements IProperty {
    I__D = setUID();
    name = '';
    fullName = '';
    type = 'personName';
    options;
    mockValue = '';
    isParent = false;
    parentRef = '';
    children;
    isValid = true;
    errors = {
        name: {
            required: false,
            duplicate: false
        }
    };

    constructor(data?: any) {
        if (data) {
            if (data.I__D) {
                this.I__D = data.I__D;
            }
            if (data.name) {
                this.name = data.name;
            }
            if (data.type) {
                this.type = data.type;
            }
            if (data.options) {
                this.options = data.options;
            }
            if (data.mockValue !== undefined) {
                this.mockValue = data.mockValue;
            }
            if (data.parentRef) {
                this.parentRef = data.parentRef;
            }
            if (data.errors) {
                this.errors = data.errors;
            }

            this.errors.name.required = !this.name;
            this.isValid = !(this.errors.name.required && this.errors.name.duplicate);
        }
    }
};


