import { findByField, isArray, deepCopy } from '@v8187/rs-utils';
import { MProperty, IProperty } from '../../models/property.model';
import { unGroupedDataTypes } from '../../configs/data-types.config';
import { getMockValue } from './mock-data';

/** 
 * Method to update fullName in given property and it's parents
 * till root level in  <parent's Full name>.<name> format
 * If, parent does not exist, then, fullName will be same as of name
 */
export const getFullName = (property: IProperty, properties: IProperty[]): IProperty => {
    // Extract Parent property
    const parentProp = properties.filter(pProp => pProp.I__D === property.parentRef)[0];

    if (parentProp) {
        /**
         * If the parent is a child of another property,
         * set the fullName for its parent too.
         * It will be repeated recursively until root level
         */
        if (parentProp.parentRef) {
            getFullName(parentProp, properties);
        }
        property.fullName = `${parentProp.fullName}.${property.name}`;
    } else {
        property.fullName = property.name;
    }
    return property;
};


/**
 * Method to get default Property name in "prop-<index>" format
 * It will ensure that each time unique name will be created.
 * 
 * For example, if we already have prop-1, prop-2, prop-3 and 
 * prop-2 is deleted and no more available. It will verify 
 * that highest available index in prop name is "3",
 * hence next prop name will be "prop-4"
 */
export const getDefaultName = (prevProperties: IProperty[]): string => {
    if (!prevProperties.length) {
        return 'prop-1';
    }
    // Extract highest Index
    let highestIndex: number = 0;
    prevProperties.map(prop => {
        // Matched name with "prop-<N>" format
        const matchedName = prop.name && prop.name.match(/prop\-(\d+)$/)
        if (matchedName && Number(matchedName[1]) > highestIndex) {
            highestIndex = Number(matchedName[1]);
        }
    });

    return `prop-${highestIndex + 1}`;
};

/**
 * Method to create a new Property instance based on given data 
 * and default name to be set
 */
export const prepareNewProperty = (prevProperties: IProperty[], objProperty?: IProperty): IProperty => {
    if (objProperty) {
        return new MProperty({
            I__D: objProperty.I__D,
            name: objProperty.name || getDefaultName(prevProperties),
            parentRef: objProperty.parentRef,
            type: objProperty.type,
            options: objProperty.options || undefined
        });
    }
    return new MProperty({ name: getDefaultName(prevProperties) });
};

/**
 * Method to Add/Update attributes 
 * ["Mock Value", "isParent" , "options"] 
 * related with "Property Type" to the given property
 */
export const updateTypeBasedAttrs = (objProperty: IProperty): IProperty => {

    let payload = deepCopy(objProperty);

    /** Bind Options, if not available */
    if (!payload.options) {
        const options: any = findByField(unGroupedDataTypes, 'name', payload.type).options;

        if (options) {
            const pOptions = payload.options = deepCopy(options);
            for (let key in pOptions) {
                if (isArray(pOptions[key])) {
                    pOptions[key] = [pOptions[key][0]];
                }
            }
        }
    }

    /** Add mock value after "options" binding */
    payload.mockValue = getMockValue(payload);

    /** Update parent hood and related options */
    payload.isParent = ['array', 'jsonObject'].indexOf(payload.type) !== -1;
    if (!payload.isParent) {
        payload.children = undefined;
    }
    return payload;
};


