import { fromEvent } from 'rxjs/observable/fromEvent';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/takeUntil';
import 'rxjs/add/operator/switchMap';
// import { map, takeUntil } from 'rxjs/operators';

import {
    DOCUMENT_CLICK, ENABLE_DOCUMENT_CLICK, DISABLE_DOCUMENT_CLICK,
    DOCUMENT_FOCUS, DISABLE_DOCUMENT_FOCUS, DISABLE_WINDOW_RESIZE,
    WINDOW_RESIZE, ENABLE_WINDOW_RESIZE, ENABLE_DOCUMENT_FOCUS
} from '../actions';

export const documentClickEpic = action$ =>
    action$
        .ofType(ENABLE_DOCUMENT_CLICK)
        .switchMap(
            () => fromEvent(document, 'click')
                .map(
                    event => ({
                        type: DOCUMENT_CLICK,
                        payload: event
                    })
                )
                .takeUntil(
                    action$.ofType(DISABLE_DOCUMENT_CLICK)
                )
        );

export const documentClickEpic2 = () =>
    fromEvent(document, 'click')
        .map(
            event => ({
                type: DOCUMENT_CLICK,
                payload: event
            })
        );

export const documentFocusEpic = action$ =>
    action$
        .ofType(ENABLE_DOCUMENT_FOCUS)
        .switchMap(
            () => fromEvent(document, 'focus')
                .map(
                    event => ({
                        type: DOCUMENT_FOCUS,
                        payload: event
                    })
                )
                .takeUntil(
                    action$.ofType(DISABLE_DOCUMENT_FOCUS)
                )
        );

export const windowResizeEpic = action$ =>
    action$
        .ofType(ENABLE_WINDOW_RESIZE)
        .switchMap(
            () => fromEvent(window, 'resize')
                .map(
                    event => ({
                        type: WINDOW_RESIZE,
                        payload: event
                    })
                ).takeUntil(
                    action$.ofType(DISABLE_WINDOW_RESIZE)
                )
        );


