import { IProperty } from '../../models/property.model';
import {
    CHANGE_PROP_TYPE,
    ADD_PROPERTIES_UI, ADD_PROPERTIES_JSON, CHANGE_PROP_OPTIONS,
    showPropOptions, updateMockSample, CHANGE_PROP_NAME, CHANGE_PROP_PARENT, REMOVE_PROPERTY, UPDATE_PROP_RELATIONSHIP as UPDATE_PROP_RELATIONSHIPS, updatePropRelationship
} from '../actions';
import {
    getMockValue, prepareNewProperty, getFullName, updateTypeBasedAttrs
} from '../helpers';

export const propertiesMiddleware = ({ dispatch, getState }) => next => action => {
    const { payload } = action;

    switch (action.type) {
        case ADD_PROPERTIES_UI:
            let count = payload || 1,
                newPropertiesUI: IProperty[] = [];

            while (count) {
                const newProp = prepareNewProperty([...getState().properties, ...newPropertiesUI]);
                newPropertiesUI.push(updateTypeBasedAttrs(newProp));
                count--;
            }
            action.payload = newPropertiesUI;
            next(action);
            dispatch(updateMockSample());
            break;
        case ADD_PROPERTIES_JSON:
            let propertiesData = payload,
                newPropertiesJson: IProperty[] = [];

            const len = propertiesData.length;

            for (let i = 0; i < len; i++) {
                const newProp = prepareNewProperty(propertiesData, propertiesData[i]);
                newPropertiesJson.push(updateTypeBasedAttrs(newProp));
            }
            action.payload = newPropertiesJson;
            next(action);
            dispatch(updatePropRelationship());
            dispatch(updateMockSample());
            break;
        case CHANGE_PROP_OPTIONS:
            /** Add mock value */
            action.payload.mockValue = getMockValue(payload);
            next(action);
            dispatch(showPropOptions(payload));
            dispatch(updateMockSample());
            break;
        case CHANGE_PROP_TYPE:
            action.payload.options = undefined;
            action.payload = updateTypeBasedAttrs(payload);
            next(action);
            dispatch(updatePropRelationship(action.payload));
            dispatch(updateMockSample());
            break;
        case UPDATE_PROP_RELATIONSHIPS:
            let { properties } = getState();
            let parentProps = properties.filter(prop => ['array', 'jsonObject'].indexOf(prop.type) !== -1);

            properties = properties.map((prop, i) => {
                if (!parentProps.some(pProp => pProp.I__D === prop.parentRef)) {
                    prop.parentRef = '';
                }
                getFullName(prop, properties);
                return prop;
            });
            action.payload = {
                parentProps,
                properties
            };
            next(action);
            break;
        case REMOVE_PROPERTY:
            next(action);
            dispatch(updatePropRelationship(action.payload));
            dispatch(updateMockSample());
            break;
        case CHANGE_PROP_PARENT:
            next(action);
            dispatch(updatePropRelationship(action.payload));
            dispatch(updateMockSample());
            break;
        case CHANGE_PROP_NAME:
            action.payload.errors.name = {
                required: !payload.name,
                duplicate: getState().properties.some(
                    prop => prop.I__D !== payload.I__D && prop.parenRef === payload.parentRef && prop.name === payload.name
                )
            };
            action.payload.isValid = !(payload.errors.name.required || payload.errors.name.isDuplicate);
            next(action);
            dispatch(updatePropRelationship(action.payload));
            dispatch(updateMockSample());
        default:
            next(action);
            break;
    };
};


